#users {
    list-style-type:none;
    padding: 1rem;
    border-radius: 1rem;
    background: white;
    width: 100%;
    box-shadow: 0 1px 1px rgba(0,0,0,0.20), 
              0 2px 2px rgba(0,0,0,0.15), 
              0 4px 4px rgba(0,0,0,0.10),
              0 8px 8px rgba(0,0,0,0.05);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}